




















































































































































































































































































import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class LogoHeader extends Vue {
  menu: any = false;
  aboutCouncil: any = false;
  policiesPriorities: any = false;
  programsEvents: any = false;
  desktop: any = false;

  mounted(): void {
    window.addEventListener("resize", () => {
      this.menu = this.desktop = this.ifDesktop();
    });
    document.addEventListener("click", e => {
      this.closeDropdowns(e);
    });
    this.menu = this.desktop = this.ifDesktop();

    window.addEventListener("load", () => {
      var els = document.querySelector(
        "link[href='https://www.canada.ca/etc/designs/canada/cdts/gcweb/v4_0_30/assets/favicon.ico']"
      );
      if (els) {
        els.setAttribute(
          "href",
          "https://www.cio.gov/assets/uswds/img/favicons/favicon-logo.png"
        );
      }
    });
  }

  beforeDestroy(): void {
    window.removeEventListener("resize", () => {
      this.menu = this.desktop = this.ifDesktop();
    });
    document.removeEventListener("click", e => {
      this.closeDropdowns(e);
    });
  }

  ifDesktop(): boolean {
    var win = window,
      doc = document,
      docElem = doc.documentElement,
      body = doc.getElementsByTagName("body")[0],
      x = win.innerWidth || docElem.clientWidth || body.clientWidth;
    if (x > 1034) return true;
    return false;
  }

  closeDropdowns(evt: Event) {
    let element = evt.target as HTMLElement;
    if (element.nodeName != "SPAN") {
      this.aboutCouncil = false;
      this.policiesPriorities = false;
      this.programsEvents = false;
    }
  }

  expanded(target: string): void {
    switch (target) {
      case "aboutCouncil":
        this.aboutCouncil = this.aboutCouncil ? false : true;
        this.policiesPriorities = false;
        this.programsEvents = false;
        break;
      case "policiesPriorities":
        this.aboutCouncil = false;
        this.policiesPriorities = this.policiesPriorities ? false : true;
        this.programsEvents = false;
        break;
      case "programsEvents":
        this.aboutCouncil = false;
        this.policiesPriorities = false;
        this.programsEvents = this.programsEvents ? false : true;
        break;
    }
  }
}
