








































































































import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class USAHeader extends Vue {
  hidden: any = false;

  mounted(): void {
    window.addEventListener("resize", () => {
      if (this.ifDesktop()) this.hidden = false;
      else this.hidden = this.ifDesktop();
    });
    if (this.ifDesktop()) this.hidden = false;
    else this.hidden = this.ifDesktop();
  }

  beforeDestroy(): void {
    window.removeEventListener("resize", () => {
      this.hidden = this.ifDesktop();
    });
  }

  ifMobileExpanded() {
    if (!this.ifDesktop() && this.hidden) return true;
    return false;
  }

  ifDesktop(): boolean {
    var win = window,
      doc = document,
      docElem = doc.documentElement,
      body = doc.getElementsByTagName("body")[0],
      x = win.innerWidth || docElem.clientWidth || body.clientWidth;
    if (x > 760) return true;
    return false;
  }
}
